import React, { Component } from 'react'
import Project from "./Project"
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider'

export default class App extends Component {
	render() {
		return (
			<MuiThemeProvider>
				<Project/>
			</MuiThemeProvider>
		)
	}
}



