import React from 'react'
import { withStyles } from 'material-ui/styles'
import Grid from 'material-ui/Grid'
import image from './puppy.jpg'

const styles = {
	heading: {
		fontSize: 28,
		lineHeight: 1
	},
	subheading: {
		marginTop: '3%',
		fontSize: 20
	},
	image: {
		width: '100%'
	}
}

class ProjectPrototype extends React.Component {
	render() {
		const { classes } = this.props
		return (
			<Grid container spacing={40}>
				<Grid className={classes.heading} item xs={12}>
					Prototype, and Testing
				</Grid>
				<Grid item xs={12}>
					We converted our wireframes into digital form in Sketch. Using inVision, we created a basic prototype that followed our ideal flow for user testing. We made several revisions based on client consultation and reviews of our research. Our main prototype took the user through the campaign creation process. We also chose to test a few ideas that we felt might be useful to users and also some wording comparison.
				</Grid>
				<Grid item xs={12}>
					Our tasks for testing went through several iterations as we planned basic tasks, further developed our prototype, received mentor advice, and further developed our tasks based on feedback and research.
				</Grid>
				<Grid item xs={12}>
					We tested the prototype with nine users. Testing was conducted in both virtual meetings and in-person. Each member of the team conducted user testing sessions.
				</Grid>
				<Grid className={classes.subheading} item xs={12}>
					Prototype
				</Grid>
				<Grid item xs={12}>
					<img className={classes.image} src={image}/>
				</Grid>
				<Grid className={classes.subheading} item xs={12}>
					Testing Takeaways
				</Grid>
				<Grid item xs={12} md={6}>
					<img className={classes.image} src={image}/>
				</Grid>
				<Grid item xs={12} md={6}>
					We immediately discovered a number of flaws in our screens. One difficulty almost every user encountered was the storytelling help we placed on some of the pages. Our wording “Pro Tips” was confusing to users, they didn’t know if it was technical help or what it was. Another issue was the placement of the storytelling “Pro Tips” help. Users didn’t notice it right away and looked for help at the top or bottom of the page with no luck. Prior to testing a particular feature, one which would allow the recipient to request volunteers to help with media for their project, the team and client realized this option would be better if offered at the very beginning of the process, when the recipient submits their proposal to eduDAO. Despite this, we tested the idea with users. The wording and (YOU MUST FINISH THIS SENTENCE MY DEAR)
				</Grid>
			</Grid>
		)
	}
}

export default withStyles(styles)(ProjectPrototype)


