import React from 'react'
import { withStyles } from 'material-ui/styles'

const styles = {
	title: {
		marginTop: '5%',
		textAlign: 'center',
		fontFamily: 'Roboto-Black',
		fontSize: 40
	},
	description: {
		marginTop: '5%'
	},
	listTitle: {
		marginTop: '3%',
		padding: 0,
		fontSize: 20
	}
}

class ProjectHeader extends React.Component {

	render() {
		const { classes } = this.props
		return (
			<div>
				<div className={classes.title}>
					Campaign for eduDAO
				</div>
				<div className={classes.description}>
					eduDAO is a nonprofit focused on educational equality through community building and a blockchain-based funding platform.
				</div>
				<div className={classes.listTitle}>
					Initial Goals
				</div>
				<ul>
					<li>Create easy and streamlined method for users to create fundraising campaigns.</li>
					<li>Design intuitive UI / UX that provides framework for Logic Model creation and visually promotes users.</li>
					<li>Differentiate from other crowdfunding sites using cryptocurrency without alienating users.</li>
				</ul>
			</div>
		)
	}
}

export default withStyles(styles)(ProjectHeader)



