import React from 'react'
import { withStyles } from 'material-ui/styles'
import Grid from 'material-ui/Grid'
import image from './puppy.jpg'

const styles = {
	heading: {
		fontSize: 28,
		lineHeight: 1
	},
	image: {
		width: '100%'
	},
	list: {
		margin: 0,
		paddingLeft: 16
	}
}

class ProjectMethods extends React.Component {

	render() {
		const { classes } = this.props
		return (
			<Grid container spacing={40}>
				<Grid className={classes.heading} item xs={12}>
					User Research Methods
				</Grid>
				<Grid item xs={12} md={6}>
					<img className={classes.image} src={image}/>
				</Grid>
				<Grid item xs={12} md={6}>
					<ul className={classes.list}>
						<li>User interviews and competitive/comparative analysis.</li>
						<li>Currently many platforms available for fundraising, most important thing was to jump in and get to know our users.</li>
						<li>Shared our assumptions about the users of the eduDAO platform through brainstorming sessions together and separately.</li>
					</ul>
				</Grid>
			</Grid>
		)
	}
}

export default withStyles(styles)(ProjectMethods)



