import React from 'react'
import { withStyles } from 'material-ui/styles'
import Grid from 'material-ui/Grid'
import image from './puppy.jpg'

const styles = {
	heading: {
		fontSize: 28,
		lineHeight: 1
	},
	image: {
		width: '100%'
	}
}

class ProjectProblem extends React.Component {

	render() {
		const { classes } = this.props
		return (
				<Grid container spacing={40}>
					<Grid className={classes.heading} item xs={12}>
						Problem
					</Grid>
					<Grid item xs={12} md={6}>
						<img className={classes.image} src={image}/>
					</Grid>
					<Grid item xs={12} md={6}>
						Educators and nonprofits face many struggles in gaining access to resources that can help students achieve more. Grants take months or even years to get approval, and many crowdfunding websites take a chunk of the funds for administrative fees and credit card companies take an additional percentage in fees. Educators need a simple and clear way to raise funds quickly. Our team was tasked with designing eduDAO’s campaign creation process to enable recipients to launch fundraising campaigns.
					</Grid>
				</Grid>
		)
	}
}

export default withStyles(styles)(ProjectProblem)


