import React from 'react'
import { withStyles } from 'material-ui/styles'
import Grid from 'material-ui/Grid'
import ProjectHeader from './ProjectHeader'
import ProjectProblem from './ProjectProblem'
import ProjectMethods from './ProjectMethods'
import ProjectInterviews from './ProjectInterviews'
import ProjectUserStory from './ProjectUserStory'
import ProjectPrototype from './ProjectPrototype'
import ProjectHighFidelity from './ProjectHighFidelity'
import ProjectChallenges from './ProjectChallenges'

const styles = {
	root: {
		flexGrow: 1,
		margin: '0 auto 0 5%',
		maxWidth: 1000,
		lineHeight: 1.8,
		fontFamily: 'Roboto-Light'
	},
	divider: {
		margin: '7% 0 3% 0',
		height: 1,
		backgroundColor: '#cccccc'
	}
}

class Project extends React.Component {

	render() {
		const { classes } = this.props
		return (
			<Grid container className={classes.root}>
				<Grid item xs={12}>
					<ProjectHeader/>
					<div className={classes.divider}/>
				</Grid>
				<Grid item xs={12}>
					<ProjectProblem/>
					<div className={classes.divider}/>
				</Grid>
				<Grid item xs={12}>
					<ProjectMethods/>
				</Grid>
				<Grid item xs={12}>
					<ProjectInterviews/>
					<div className={classes.divider}/>
				</Grid>
				<Grid item xs={12}>
					<ProjectUserStory/>
					<div className={classes.divider}/>
				</Grid>
				<Grid item xs={12}>
					<ProjectPrototype/>
					<div className={classes.divider}/>
				</Grid>
				<Grid item xs={12}>
					<ProjectHighFidelity/>
					<div className={classes.divider}/>
				</Grid>
				<Grid item xs={12}>
					<ProjectChallenges/>
				</Grid>
			</Grid>
		)
	}
}

export default withStyles(styles)(Project)



