import React from 'react'
import { withStyles } from 'material-ui/styles'
import Grid from 'material-ui/Grid'
import image from './puppy.jpg'

const styles = {
	heading: {
		fontSize: 28,
		lineHeight: 1
	},
	subheading: {
		marginTop: '3%',
		fontSize: 20
	},
	image: {
		width: '100%'
	}
}

class ProjectHighFidelity extends React.Component {
	render() {
		const { classes } = this.props
		return (
			<Grid container spacing={40}>
				<Grid className={classes.heading} item xs={12}>
					High-Fidelity
				</Grid>
				<Grid item xs={12}>
					We developed a campaign creation platform that helps the recipient tell their story while using donor research to inform what the recipient shares about their mission. From initial research and ideation to high fidelity prototypes, we incorporated user needs, goals, and client feedback to develop a simple, clean form process and campaign preview/landing page.
				</Grid>
				<Grid className={classes.subheading} item xs={12}>
					Style Tiles
				</Grid>
				<Grid item xs={12}>
					<img className={classes.image} src={image}/>
				</Grid>
				<Grid item xs={12}>
					<img className={classes.image} src={image}/>
				</Grid>
				<Grid className={classes.subheading} item xs={12}>
					High-Fidelity Prototype
				</Grid>
				<Grid item xs={12}>
					<img className={classes.image} src={image}/>
				</Grid>
				<Grid item xs={12}>
					<img className={classes.image} src={image}/>
				</Grid>
			</Grid>
		)
	}
}

export default withStyles(styles)(ProjectHighFidelity)


